.book-info-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    font-family: 'Arial, sans-serif';
}

.book-details {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.book-header {
    display: flex;
    align-items: flex-start;
    width: 100%;
}

.book-cover {
    width: 200px; /*      */
    height: auto;
    margin-right: 20px;
}

.book-meta {
    flex: 1;
}

.book-meta h1 {
    font-size: 2em;
    margin-bottom: 10px;
}

.book-meta p {
    margin: 5px 0;
}

.book-meta-details {
    display: flex;
    flex-wrap: wrap; /*         */
}

.book-meta-details p {
    flex: 1 1 50%; /*       */
    margin: 5px 0; /*      */
}
.purchase-button {
    display: inline-block;
    padding: 10px 20px;
    background-color: #007BFF;
    color: white;
    text-decoration: none;
    border-radius: 5px;
    margin-top: 10px;

}

.book-description {
    margin-top: 20px;
}

.book-description h2 {
    font-size: 1.5em;
    margin-bottom: 10px;
}
