.sliders-container {
    text-align: center; /*    */
}

.slider-item {
    display: flex;
    flex-direction: column;
    justify-content: center; /*     */
    align-items: center; /*     */
    height: 200px; /*     */
    overflow: hidden;
}

.slider-image {
    max-width: 100%; /*    50%  */
    height: 100%; /*     */
    margin: 0 auto; /*     */
    transition: transform 0.5s ease-in-out; /*     */

}

.slider-image-size{
        width:100%;
        height:100%;
        object-fit:fill;
        transition: transform 0.5s ease-in-out; /*     */
}

.second-slider-container .slick-slide {
    transition: opacity 0.5s ease-in-out; /*     */
    display: inline-block;
}
.second-slider-container{
    width: 50%;
    max-width: 1200px;
    margin: 0 auto;
    overflow: hidden;
}

.slider-image:hover,
.slider-image-size:hover {
    transform: scale(1.05); /*     */
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

@media (prefers-reduced-motion: no-preference) {
    .slider-image {
        animation: App-logo-spin infinite 20s linear;
    }

}

@media (max-width: 480px) {
    .slider-image {
        max-width: 80%;
    }

    .slider-image-size{
        max-width: 80%; /*    50%  */
    }
}

@media (max-width: 768px) {
    .slider-image {
        max-width: 80%;
    }
}
