/* footer.css */
.footer-59391 {
    background-color: #EAD8C0; /*  */
    color: #333; /*   */
    text-align: center; /*    */
    width: 100%; /*     */
    font-size: 14px; /*    */
    padding: 20px 0; /*   */
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /*   */
    position: relative; /*    */
}

.footer-59391 .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.footer-59391 .site-logo a {
    font-size: 24px; /*    */
    color: #333;
    text-decoration: none;
}

.footer-59391 .social-icons {
    display: flex;
    justify-content: flex-end;
    list-style: none;
    padding: 0;
}

.footer-59391 .social-icons li {
    margin: 0 10px;
}

.footer-59391 .social-icons a {
    color: #333;
    font-size: 18px;
    text-decoration: none;
}

.footer-59391 .nav-links {
    display: flex;
    justify-content: flex-end;
    list-style: none;
    padding: 0;
}

.footer-59391 .nav-links li {
    margin: 0 10px;
}

.footer-59391 .nav-links a {
    color: #333;
    font-size: 14px;
    text-decoration: none;
}

.footer-59391 .copyright p {
    margin: 0;
}
